<template>
	<q-dialog
		:value="widget !== null"
		full-height
		persistent
		>
		
		<q-card class="column full-height" style="width: 1400px; max-width: 90vw;" v-if="widget !== null">
			<q-card-section class="row items-center">
				<div class="text-h6">{{ $t('Widget') }} {{ widget.title }}</div>
				
				<q-space />
				<q-btn flat round color="grey" icon="close" @click="cancel()" />
			</q-card-section>
			<q-separator />
			
			<q-card-section class="col scroll body">
				
				<!-- WIDGET BOX -->
				<div class="text-h6 primary q-ma-sm">{{ $t('Widget-Box') }}</div>
				<q-separator style="margin: 0 -16px" />
				
				<div class="row">
					<div class="col">
						<inputs @onChange="onChange" id="icon" :label="$t('Widget Icon')" :value="widget.icon || ModuleSettings.icon || ''" :icon="widget.icon" />
					</div>
					<div class="col">
						<inputs @onChange="onChange" id="title" :label="$t('Widget Title')" :value="widget.title || ''" autofocus />
					</div>
					<div class="col">
						<inputs @onChange="onChange" id="link" :label="$t('Widget Link')" :value="widget.link || ''" info="config#HomeKitTile#jumper#info" />
					</div>
					<div class="col">
						<inputs type="switch" @onChange="onChange" id="hideTitle" :label="$t('Hide Widget Title and Icon')" :value="widget.hideTitle || false" />
					</div>
				</div>
				
				<!-- MODULE CONFIGURATION -->
				<div class="text-h6 primary q-ma-sm">{{ $t('Module') }}</div>
				<q-separator style="margin: 0 -16px" />
				
				<div class="row">
					<div class="col-3">
						<inputs @onSelect="onChange" id="module" :label="$t('Module')" type="select" :options="ModuleList" :value="widget.module" options-dense use-input required :error="errors['module'] !== undefined" :error-message="errors['module']" />
					</div>
					<div class="col-9">
						<div class="centered" v-show="widget.module">{{ $t(widget.module + '#description') }}</div>
					</div>
				</div>
				
				<div v-show="widget.module">
					
					<!-- MODULE CONFIGURAGION -->
					<div class="text-h7 primary q-ma-sm">{{ $t('Module Configuration') }}</div>
					<q-separator style="margin: 0 0 8px 0" />
					
					<div class="row" v-show="ModuleConfigurations.length > 0">
						<template v-for="ModuleConfig in ModuleConfigurations">
						<div :class="'col-' + (ModuleConfig.columns || 4)" :key="ModuleConfig.parameter" v-if="ModuleConfig.hide !== true" v-show="dependencies(ModuleConfig.dependencies)">
							<inputs
								@onChange="onChange({ ...arguments[0], 'id': 'config.' + ModuleConfig.parameter })"
								@onSelect="onChange({ ...arguments[0], 'id': 'config.' + ModuleConfig.parameter })"
								v-bind="ModuleConfig"
								:value="getValueForInput(ModuleConfig)"
								/>
							
						</div>
						</template>
					</div>
					<div class="row" v-show="ModuleConfigurations.length === 0">
						<p class="q-pa-sm">
							{{ $t('No configuration options for this module') }}
						</p>
					</div>
					
					<!-- MODULE CUSTOM COMPONENT -->
					<div class="row" v-if="ModuleSettings && ModuleSettings.customConfiguration">
						<component
							:is="ModuleSettings.customConfiguration"
							:config="widget.config"
							@onChange="onChange"
							/>
						
					</div>
					
					<!-- MODULE DEVICES -->
					<template v-if="ModuleSettings.addDevice !== false">
					<div class="text-h7 primary q-ma-sm">{{ $t('Module Devices') }}</div>
					<q-separator style="margin: 0 -0 8px 0" />
					
					<div class="row">
						<widget-devices-section
							@onChange="onChange"
							:itemList="widget.items || []"
							:module="widget.module"
							/>
						
					</div>
					</template>
				</div>
			</q-card-section>
			
			<q-separator />
			<q-card-actions align="right">
				<q-btn flat :label="$t('Cancel')" color="negative" @click="cancel()" />
				<q-btn flat :label="$t('Apply')" color="primary" @click="save" :disable="hasErrors" />
			</q-card-actions>
		</q-card>
	</q-dialog>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import WidgetDevicesSection from './WidgetDevicesSection.vue';

import Modules from 'src/modules/'

const _rfdc = require('rfdc')();
export default defineComponent({
	'name': 'WidgetPage',
	'props': [ 'widgetId' ],
	
	'components': {
		WidgetDevicesSection
	},
	
	data() {
		this.ModuleList = Modules._ModuleList.map(module => ({ 'label': module, 'value': module }));
		
		return {
			'component': null,
			'errors': {},
			'ModuleConfigurations': [],
			'ModuleSettings': {},
			'widget': null
		}
	},
	
	'computed': {
		hasErrors() {
			return Object.keys(this.errors).length > 0;
		}
	},
	
	'methods': {
		dependencies(dependencies) {
			if (!dependencies) {
				return true;
			}
			
			return Object.keys(dependencies).some(dependency => this.widget.config[dependency] === dependencies[dependency]);
		},
		getValueForInput(ModuleConfig) {
			return this.widget && this.widget.config[ModuleConfig.parameter] !== undefined ? this.widget.config[ModuleConfig.parameter] : (typeof ModuleConfig.value === 'function' ? ModuleConfig.value() : ModuleConfig.value);
		},
		onChange(props) {
			const { _error, id, value } = props;
			
			// error
			if (_error) {
				this.$set(this.errors, id, _error);
			}
			
			// config
			else if (id.indexOf('config.') > -1) {
				this.$delete(this.errors, id);
				this.$set(this.widget.config, id.replace('config.', ''), value);
			}
			
			// no error, thus set data
			else {
				this.$delete(this.errors, id);
				this.$set(this.widget, id, value);
				
				// module change
				if (id === 'module') {
					this.ModuleConfigurations = Modules._ModuleConfigurations[value] || [];
					this.ModuleSettings = Modules._ModuleSettings[value] || {};
					this.$set(this.widget, 'config', {});
				}
			}
		},
		cancel(widget = null) {
			this.$emit('onClose', widget);
			this.widget = null;
			this.errors = {};
		},
		save() {
			this.$store.commit('editor/addWidget', { 'widget': this.widget });
			this.cancel(this.widget);
		}
	},
	
	'watch': {
		widgetId(id) {
			if (id === null) {
				this.widget = null;
			}
			else {
				this.widget = this.$store.state.editor.widgets[id] ? _rfdc(this.$store.state.editor.widgets[id]) : { id, 'config': {} };
				this.ModuleConfigurations = Modules._ModuleConfigurations[this.widget.module] || [];
				this.ModuleSettings = Modules._ModuleSettings[this.widget.module] || {};
			}
		}
	}
});
</script>

<style scoped>
.centered {
	display: flex;
	align-items: center;
	height: 80%;
	padding-left: 16px;
}
</style>
