<template>
	<div>
		<!-- WIDGET -->
		<widget-popup
			@onClose="selectedWidget = null"
			@onSave="saveWidgetAssignments"
			:widget="selectedWidget"
			/>
		
		<!-- WIDGETS -->
		<q-expansion-item
			@input="expanded = !expanded"
			:value="disabled !== true ? expanded : false"
			class="full-width paper"
			:disable="disabled"
			>
			
			<template v-slot:header>
				<q-item-section avatar>
					<icon :name="icon" />
				</q-item-section>

				<q-item-section style="height: 40px">
					{{ label }}
				</q-item-section>

				<q-item-section side v-if="id !== 'desktop'" class="notDisabled">
					<inputs @onSelect="onChange" type="select" :options="options" dense :label="$t('Use same layout as')" :id="id + 'SameAs'" @click.native.prevent="stopPropagation" :value="widgetConfig[id + 'SameAs'] || 'desktop'" style="width: 150px" />
				</q-item-section>
			</template>
			
			<div>
				<!-- FULLSCREEN -->
				<div v-show="fullscreen === true">
					<div style="padding: 0 16px 8px 16px; min-height: 64px" v-show="widgets.length === 0" class="row">
						<q-btn color="secondary" icon="mdi-plus-circle" :label="$t('add widget')" @click="addWidget" style="margin: auto 0" />
					</div>
					<q-separator />
					
					<div class="grid-item tooltip-widget" v-show="widgets[0] !== undefined">
						<div class="row justify-between">
							<div class="row items-center" style="padding-left: 8px">
								{{ $t('Fullscreen') }}
							</div>
							<div class="row">
								<q-btn size="sm" flat round icon="mdi-pencil" @click="selectedWidget = widgets[0]" />
								<q-btn size="sm" flat round icon="mdi-content-copy" @click="copyWidget(widgets[0].i)" />
								<q-btn size="sm" flat round icon="mdi-delete" color="negative" @click="deleteWidget(widgets[0].i)" />
							</div>
						</div>
					</div>
				</div>
				
				<!-- WIDGETS -->
				<div v-show="fullscreen !== true">
					<div style="padding: 0 16px 8px 16px; min-height: 64px" class="row">
						<q-btn color="secondary" icon="mdi-plus-circle" :label="$t('add widget')" @click="addWidget" style="margin: auto 0" />
						<inputs dense type="switch" inline :label="$t('Height of all widgets equal to page height (no scrollbars)')" :id="id + 'FitPageHeight'" @onChange="onChange" :value="widgetConfig[id + 'FitPageHeight'] || false" style="margin: auto 0 auto 16px" />
						<inputs :set="rowHeight = (parseInt(widgetConfig[id + 'FitPageHeight'] !== true && widgetConfig[id + 'WidgetHeight']) || 100)" dense type="number" v-show="widgetConfig[id + 'FitPageHeight'] !== true" :label="$t('Widget height') + ' (' + $t('in pixels') + ')'" :id="id + 'WidgetHeight'" @onChange="onChange" :value="widgetConfig[id + 'WidgetHeight'] || 100" style="margin: auto 0 auto 16px" />
						<inputs :set="gapSize = parseInt(widgetConfig[id + 'GapSize'] || 5)" dense type="number" :label="$t('Widget gaps') + ' (' + $t('in pixels') + ')'" :id="id + 'GapSize'" @onChange="onChange" :value="widgetConfig[id + 'GapSize'] || 5" style="margin: auto 0 auto 16px" />
					</div>
					<q-separator />
					
					<div style="height: 800px; overflow-y: scroll">
						<grid-layout
							@layout-updated="setWidgets"
							:layout="widgets"
							:row-height="rowHeight"
							:margin="[ gapSize, gapSize ]"
							:use-css-transforms="true"
							:responsive="true"
							style="min-height: 50px"
							>
							
							<grid-item
								v-for="widget in widgets"
								:x="widget.x"
								:y="widget.y"
								:w="widget.w"
								:h="widget.h"
								:i="widget.i"
								:key="widget.i"
								class="grid-item tooltip-widget"
								:class="{ 'rounded-borders': widgetEdges !== true }"
								>
								
								<q-tooltip target=".tooltip-widget">
									{{ getWidgetsData(widget.items) }}
								</q-tooltip>
								<div class="row justify-between">
									<div class="row items-center" style="padding-left: 8px">
										{{ $t('Position') + ': ' + widget.x + 'x * '+ widget.y + 'y' }} -
										{{ $t('Size') + ': ' + widget.w + 'w (' + (100 / (12 / widget.w)).toFixed(2) + '%) * '+ widget.h + 'h (' + (widget.h * rowHeight) + 'px)' }}
									</div>
									<div class="row">
										<q-btn size="sm" flat round icon="mdi-pencil" @click="selectedWidget = widget" />
										<q-btn size="sm" flat round icon="mdi-content-copy" @click="copyWidget(widget.i)" />
										<q-btn size="sm" flat round icon="mdi-delete" color="negative" @click="deleteWidget(widget.i)" />
									</div>
								</div>
								<div class="row q-pa-sm tooltip-widget" style="overflow: hidden">
									{{ getWidgetsData(widget.items) }}
									<q-tooltip>
										{{ getWidgetsData(widget.items) }}
									</q-tooltip>
								</div>
							</grid-item>
							<div class="grid-spacer" :style="{ bottom: (-3 * rowHeight) + 'px', height: (3 * rowHeight) + 'px' }"></div>
						</grid-layout>
					</div>
				</div>
			</div>
		</q-expansion-item>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import { v4 as uuid } from 'uuid';

import WidgetPopup from './WidgetPopup.vue';

import { ucFirst } from 'src/helpers/utils';
import VueGridLayout from 'vue-grid-layout';

const _rfdc = require('rfdc')();
export default defineComponent({
	'name': 'WidgetSection',
	'props': [
		'pageId',
		'tabId',
		'id',
		'label',
		'icon',
		'fullscreen',
		'widgetEdges',
		'widgetConfig',
		'items'
	],
	
	'components': {
		'GridLayout': VueGridLayout.GridLayout,
		'GridItem': VueGridLayout.GridItem,
		WidgetPopup
	},
	
	'watch': {
		fullscreen(val) {
			// add widget when switching to fullscreen
			if (val === true && this.widgets.length === 0) {
				this.addWidget();
			}
		}
	},
	
	data() {
		return {
			'expanded': this.id === 'desktop',
			'selectedWidget': null,
			'widgets': _rfdc(this.items) || [],
			'maxHeight': 0
		}
	},
	
	'computed': {
		disabled() {
			return this.id !== 'desktop' && this.widgetConfig[this.id + 'SameAs'] !== 'specific-layout';
		},
		options() {
			let options = [
				{
					'label': this.$i18n.t('wie Desktop'),
					'value': 'desktop'
				},
				{
					'label': this.$i18n.t('wie Tablet'),
					'value': 'tablet'
				},
				{
					'label': this.$i18n.t('wie Smartphone'),
					'value': 'smartphone'
				},
				{
					'label': this.$i18n.t('eigenes Layout'),
					'value': 'specific-layout'
				}
			];
			
			options = options.filter(option => option.value !== this.id);
			return options;
		}
	},
	
	'methods': {
		stopPropagation(e) {
			e.stopPropagation();
		},
		onChange(props) {
			this.$store.commit('editor/setWidgetConfig', {
				'pageId': this.pageId,
				'tabId': this.tabId,
				'key': props.id,
				'val': props.value
			});
		},
		getWidget(widgetId) {
			return this.$store.state.editor.widgets[widgetId] || { 'items': [] };
		},
		getWidgetsData(widgetIds) {
			if (!widgetIds || widgetIds.length === 0) {
				return this.$i18n.t('No widgets assigned');
			}
			else {
				return widgetIds.map(widgetId => {
					const widget = this.getWidget(widgetId);
					return widget.module +
							(!widget.title
? ''
: ' (' + widget.title +
								(widget.module !== 'StateList' ? '' : ', ' + widget.items.length + ' ' + this.$i18n.t('items') + ' ' + this.$i18n.t('apprx.') + ' ' + widget.items.length * 48 + 'px ' + this.$i18n.t('in height')) +
							')');
				}).join(', ');
			}
		},
		setWidgets(widgets) {
			this.$store.commit('editor/setWidgets', {
				'pageId': this.pageId,
				'tabId': this.tabId,
				'key': 'widgets' + ucFirst(this.id),
				'val': widgets
			});
		},
		addWidget() {
			this.widgets.push({
				'x': (this.widgets.length * 2) % 12,
				'y': this.widgets.length + 12,
				'w': 4,
				'h': 4,
				'i': uuid()
			});
		},
		copyWidget(widgetId) {
			const widget = _rfdc(this.widgets.find(widget => widget.i === widgetId));
			widget.i = uuid();
			this.widgets.push(widget);
		},
		deleteWidget(widgetId) {
			const confirmation = confirm(this.$i18n.t('Really delete item?'));
			if (confirmation) {
				this.widgets.splice(this.widgets.findIndex(widget => widget.i === widgetId), 1);
			}
		},
		saveWidget(widget) {
			const widgetIndex = this.widgets.findIndex(w => w.i === widget.i);
			this.widgets.splice(widgetIndex, 1, widget);
			this.setWidgets(this.widgets);
		},
		saveWidgetAssignments(items) {
			this.widgets.splice(this.widgets.findIndex(w => w.i === this.selectedWidget.i), 1, {
				...this.selectedWidget,
				items
			});
			
			this.selectedWidget = null;
			this.setWidgets(this.widgets);
		}
	}
});
</script>

<style lang="scss">
.grid-item {
	background-color: var(--q-color-primary);
	color: #fff;
	touch-action: none;
	overflow: hidden;
	min-height: 50px;
	text-align: left;
	font-size: 10px;
}

.grid-spacer {
	position: absolute;
	width: 100%;
}
</style>
