<template>
	<div class="full-width">
		<div class="row">
			<div class="col-4">
				<q-select
					@filter="filter"
					ref="addDeviceSelect"
					:label="$t('Add Device')"
					:options="optionGroupsAddDevice"
					value=""
					use-input
					stack-label
					style="margin: 0 8px"
					v-show="ModuleSettings.addDevice !== false && (!ModuleSettings.addDevice || (ModuleSettings.addDevice && (ModuleSettings.addDevice.max === undefined || ModuleSettings.addDevice.max > items.length)))"
					>
					
					<template v-slot:option="scope">
						<q-expansion-item
							expand-separator
							header-class="text-weight-bold"
							:label="scope.opt.label"
							:icon="scope.opt.icon"
							>
							
							<template v-for="device in scope.opt.devices">
								<q-item
									:key="device.id"
									clickable
									v-ripple
									@click="addDevice(device.id)"
									>
									
									<q-item-section>
										<q-item-label class="q-ml-md">{{ device.name }}</q-item-label>
									</q-item-section>
								</q-item>
							</template>
						</q-expansion-item>
					</template>
				</q-select>
			</div>
			<div v-show="ModuleSettings.addGroup !== false" class="col-1" style="text-align: center; line-height: 70px">{{ $t('or') }}</div>
			<div v-show="ModuleSettings.addGroup !== false" class="col-3">
				<inputs
					label="Add Group"
					@onChange="group = arguments[0]"
					v-on:keyup.enter="addGroup"
					:value="group.value || ''"
					>
					
					<template slot:end>
						<q-btn v-show="group.value" flat size="sm" dense icon="mdi-check" :label="$t('add group')" style="height: 32px; min-width: 100px; margin-top: 16px" @click="addGroup" />
					</template>
				</inputs>
			</div>
			<div v-show="ModuleSettings.addDivider !== false" class="col-1" style="text-align: center; line-height: 70px">{{ $t('or') }}</div>
			<div v-show="ModuleSettings.addDivider !== false" class="col-3" style="line-height: 70px">
				<q-btn
					icon="mdi-minus"
					:label="$t('Add Divider')"
					color="primary"
					@click="addDivider"
					/>
				
			</div>
		</div>
		<div class="row">
			<q-list class="full-width" style="margin-top: 16px">
				<draggable
					v-model="items"
					handle=".dragndrop"
					@change="emitChange"
					>
					
					<transition-group type="transition" name="flip-list" tag="div">
						<div v-for="item in items" :key="item.id" class="list-group-item" :set="isInGroup = isInGroup && item.type === 'divider' ? false : (item.type === 'group' ? true : isInGroup)">
							<q-item>
								<q-item-section avatar>
									<div class="row items-center">
										<icon v-show="items.length > 1" name="mdi-drag-horizontal" class="dragndrop" style="padding-right: 16px" />
										
										<inputs type="button" icon="mdi-content-copy" :tooltip="$t('copy')" @click="copyItem($event, item.id)" />
										<inputs type="button" icon="mdi-delete" :tooltip="$t('delete')" color="negative" @click="deleteItem($event, item.id)" v-show="item._deleted !== true" />
										<inputs type="button" icon="mdi-restore" :tooltip="$t('restore')" color="positive" @click="restoreItem($event, item.id)" v-show="item._deleted === true" class="restore" />
									</div>
								</q-item-section>
								
								<!-- RENDER DEVICE, GROUP or DIVIDER -->
								<template v-if="item.type !== 'device' || (item.type === 'device' && deviceList[item.deviceId] !== undefined)">
								
								<!-- BODY -->
								<q-item-section>
									<span v-if="item.type === 'divider'"><em>{{ '(' + $t('divider') + ')' }}</em></span>
									<span v-if="item.type === 'group'"><strong>{{ item.name }}</strong></span>
									<span v-if="item.type === 'device'"><strong>{{ deviceList[item.deviceId].name }}</strong> ({{ deviceList[item.deviceId].function + ', #' + item.deviceId }})</span>
									
									<!-- GROUP -->
									
									<!-- DEVICE -->
									<div v-if="item.type === 'device'" class="row nowrap">
										<inputs v-if="shallBeShown('label')" dense style="width: 150px" @onChange="onChange" label="Label" :itemId="item.id" id="label" :value="item.label || (deviceList[item.deviceId].states[item.bodyStateKey] && deviceList[item.deviceId].states[item.bodyStateKey].label) || ''" :placeholder="deviceList[item.deviceId].label || deviceList[item.deviceId].name" />
										<inputs v-if="shallBeShown('bodyStateKey')" dense style="width: 130px" @onSelect="onChange" label="Body State" :itemId="item.id" id="bodyStateKey" :value="item.bodyStateKey" type="select" :options="deviceStatesOptions(item.deviceId)" />
										<inputs v-if="shallBeShown('bodyElement')" v-show="item.bodyStateKey" dense style="width: 230px" @onSelect="onChange" @onOptions="onChange" id="bodyElement" label="Body Element" :itemId="item.id" :value="item.bodyElement" info="mdi-plus" type="select.components" :components="FUNCTION_COMPONENTS['bodyElements'][deviceFunction(item)]" :componentValues="componentValues(item)" />
									</div>
								</q-item-section>
								
								<!-- CUSTOM DEVICE SECTION -->
								<q-item-section v-if="ModuleSettings.addDevice && ModuleSettings.addDevice.customDeviceSection">
									<component
										:is="ModuleSettings.addDevice.customDeviceSection"
										@onChange="onChange"
										:item="item"
										/>
									
								</q-item-section>
								
								<!-- ACTION -->
								<q-item-section side>
									
									<!-- GROUP -->
									<div v-if="item.type === 'group'" class="row nowrap">
										<inputs dense style="width: 100px" @onChange="onChange" :label="$t('Group Actions')" :itemId="item.id" id="actions" :value="item.actions || false" type="switch" />
										<inputs v-if="shallBeShown('groupElement')" v-show="item.actions === true" dense style="width: 200px" @onSelect="onChange" @onOptions="onChange" id="groupElement" label="Group Element" :itemId="item.id" :value="item.groupElement" info="mdi-plus" type="select.components" :components="FUNCTION_COMPONENTS['actionElements']['_defaults'].filter(component => component.value !== 'InputAction' && component.value !== 'DropdownAction')" :componentValues="componentValues(item)" />
									</div>
									
									<!-- DEVICE -->
									<div v-if="item.type === 'device'" class="row nowrap">
										<inputs v-if="shallBeShown('primaryStateKey')" dense style="width: 130px" @onSelect="onChange" label="Action State" :itemId="item.id" id="primaryStateKey" :value="item.primaryStateKey || deviceStates(item.deviceId)[0]" type="select" :options="deviceStatesOptions(item.deviceId)" />
										<inputs v-if="shallBeShown('showState')" v-show="item.primaryStateKey" dense style="width: 100px" @onChange="onChange" label="Show State" :itemId="item.id" id="showState" :value="item.showState !== undefined ? item.showState : true" type="switch" />
										<inputs v-if="shallBeShown('showGroupAction')" v-show="item.primaryStateKey && isInGroup === true" dense style="width: 100px" @onChange="onChange" label="Use in Actions" :itemId="item.id" id="useInGroup" :value="item.useInGroup !== undefined ? item.useInGroup : true" type="switch" />
										<inputs v-if="shallBeShown('actionElement')" v-show="item.primaryStateKey" dense style="width: 200px" @onSelect="onChange" @onOptions="onChange" id="actionElement" label="Action Element" :itemId="item.id" :value="item.actionElement" info="mdi-plus" type="select.components" :components="FUNCTION_COMPONENTS['actionElements'][deviceFunction(item)]" :componentsValues="componentValues(item, 'action', 'primary')" :device="getDevice(item.deviceId)" />
									</div>
									<div v-if="item.type === 'device'" class="row nowrap">
										<q-select
											v-if="shallBeShown('secondaryStatesKeys')"
											stack-label
											dense
											style="width: 430px"
											@input="onChangeSecondaryStatesKeys(item.id, arguments[0])"
											:label="$t('Secondary State')"
											:value="item.secondaryStatesKeys"
											:options="deviceStatesOptions(item.deviceId)"
											use-chips
											multiple
											/>
										
									</div>
								</q-item-section>
								</template>
								
								<!-- DEVICE NOT FOUND -->
								<q-item-section v-else>
									{{ $t('Device not found') + ' (' + $t('Device ID') + ': ' + item.deviceId + ')' }}
								</q-item-section>
								
							</q-item>
							<q-separator />
							
						</div>
					</transition-group>
				</draggable>
			</q-list>
		</div>
	</div>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';
import { v4 as uuid } from 'uuid';

import WidgetDevicesSection from './WidgetDevicesSection.vue';
import Draggable from 'vuedraggable';

import Modules from 'src/modules/';
import { listOfComponents as FUNCTION_COMPONENTS } from 'src/functions/';

const _rfdc = require('rfdc')();
const _merge = require('deepmerge');
export default defineComponent({
	'name': 'WidgetDevicesSection',
	'props': [ 'module', 'itemList' ],
	
	'components': {
		Draggable,
		WidgetDevicesSection
	},
	
	data() {
		this.FUNCTION_COMPONENTS = FUNCTION_COMPONENTS;
		const optionsAddDevice = this.$store.getters['editor/getGroups']('function');
		
		return {
			'components': FUNCTION_COMPONENTS,
			'deviceId': null,
			'group': {},
			'isInGroup': false,
			'items': _rfdc(this.itemList),
			'deviceList': this.$store.state.editor.devices,
			'optionsAddDevice': optionsAddDevice,
			'optionGroupsAddDevice': Object.values(optionsAddDevice),
			'optionGroupsAddDeviceDefault': Object.values(optionsAddDevice),
			'ModuleSettings': Modules._ModuleSettings[this.module] || {}
		}
	},
	
	'watch': {
		module(module) {
			this.ModuleSettings = Modules._ModuleSettings[module] || {};
		}
	},
	
	'methods': {
		shallBeShown(field) {
			return !this.ModuleSettings.addDevice || (this.ModuleSettings.addDevice && (!this.ModuleSettings.addDevice.fields || (this.ModuleSettings.addDevice.fields && this.ModuleSettings.addDevice.fields.includes(field))));
		},
		filter(inputValue, doneFn) {
			const results = !inputValue
								? this.optionGroupsAddDeviceDefault
								: Object.values(this.$store.getters['editor/getGroups']('function', inputValue));
			
			doneFn(() => {
				this.optionGroupsAddDevice = results;
			});
		},
		componentValues(item, type = 'body', stateKey = 'body') {
			const state = (this.deviceList[item.deviceId] && this.deviceList[item.deviceId].states[item[stateKey + 'StateKey']]) || {};
			const component = item[type + 'Element'];
			return _merge(state[component + 'Config'] || {}, (item[component + 'Config'] && item[component + 'Config'][item.primaryStateKey]) || {});
		},
		getDevice(deviceId) {
			return this.deviceList[deviceId];
		},
		deviceFunction(item) {
			return this.deviceList[item.deviceId].function === 'other' ? '_defaults' : this.deviceList[item.deviceId].function;
		},
		deviceStates(deviceId) {
			return Object.keys(this.deviceList[deviceId].states) || [];
		},
		deviceStatesOptions(deviceId) {
			return this.deviceStates(deviceId).map(stateKey => ({
				'label': stateKey,
				'value': stateKey
			}));
		},
		hasDevices(groupId) {
			return this.optionsAddDevice[groupId].devices.length > 0;
		},
		emitChange() {
			this.$emit('onChange', {
				'id': 'items',
				'value': this.items
			});
		},
		deleteItem(e, itemId) {
			e.stopPropagation();
			this.$delete(this.items, this.items.findIndex(item => item.id === itemId));
			this.emitChange();
		},
		copyItem(e, itemId) {
			e.stopPropagation();
			const copy = _rfdc(this.items.find(item => item.id === itemId));
			this.addItem(copy);
		},
		/*
		restoreItem(e, deviceId) {
			e.stopPropagation();
			this.emitChange();
		},
		*/
		addItem(props) {
			const id = uuid();
			this.items.push({ ...props, id });
			this.emitChange();
		},
		addDevice(deviceId) {
			this.addItem({ 'type': 'device', deviceId });
			
			if (this.ModuleSettings.addDevice && this.ModuleSettings.addDevice.max !== undefined && this.ModuleSettings.addDevice.max <= this.items.length) {
				this.$refs.addDeviceSelect.hidePopup();
			}
		},
		addGroup() {
			this.addItem({ 'type': 'group', 'name': this.group.value });
			this.group = {};
		},
		addDivider() {
			this.addItem({ 'type': 'divider' });
		},
		onChange(props) {
			if (props.id && props.itemId) {
				const itemIndex = this.items.findIndex(item => item.id === props.itemId);
				if (itemIndex > -1) {
					// Module Config
					if (props.id.indexOf('moduleConfig.') > -1) {
						this.$set(this.items, itemIndex, {
							...this.items[itemIndex],
							'moduleConfig': {
								...this.items[itemIndex].moduleConfig || {},
								[props.id.replace('moduleConfig.', '')]: props.value
							}
						});
					}
					
					// Component Options
					else if (props.id === 'componentOptions') {
						const stateKey = this.items[itemIndex].primaryStateKey;
						this.$set(this.items[itemIndex], props.component + 'Config', {
							...this.items[itemIndex][props.component + 'Config'] || {},
							[stateKey]: {
								...(this.items[itemIndex][props.component + 'Config'] && this.items[itemIndex][props.component + 'Config'][stateKey]) || {},
								...props.value
							}
						});
					}
					
					// any other data
					else {
						this.$set(this.items, itemIndex, {
							...this.items[itemIndex],
							[props.id]: props.value
						});
					}
					
					// emit change
					this.$emit('onChange', {
						'id': 'items',
						'value': this.items
					});
				}
			}
		},
		onChangeSecondaryStatesKeys(itemId, args) {
			this.onChange({
				'value': args.map(argument => argument.value || argument),
				'itemId': itemId,
				'id': 'secondaryStatesKeys'
			});
		}
	}
});
</script>
