<template>
	<div>
		<div class="text-h6 primary q-ma-sm">{{ $t('Page') }}: {{ page.title }}</div>
		<div class="row">
			<div class="col">
				<inputs @onChange="onChange" id="icon" :label="$t('Page Icon')" :value="page.icon || ''" :icon="page.icon" />
			</div>
			<div class="col">
				<inputs @onChange="onChange" id="title" :label="$t('Page Title')" :value="page.title || ''"  />
			</div>
		</div>
		<div class="row">
			<div class="col">
				<inputs type="color" @onChange="onChange" id="iconColor" :label="$t('Icon Color')" :value="page.iconColor || ''"  />
			</div>
			<div class="col">
				<inputs type="color" @onChange="onChange" id="color" :label="$t('Font Color')" :value="page.color || ''"  />
			</div>
			<div class="col">
				<inputs type="color" @onChange="onChange" id="backgroundColor" :label="$t('Background Color')" :value="page.backgroundColor|| ''" />
			</div>
		</div>
		
		<div class="text-h6 primary q-ma-sm">{{ $t('Tabs of page') + ' ' + page.title }}</div>
		<div class="q-ma-sm row">
			<q-btn color="secondary" icon="mdi-plus-circle" :label="$t('add tab')" @click="addTab" />
			<inputs type="switch" inline :label="$t('Tab Labels nicht anzeigen (nur Icons)')" id="hideLabels" @onChange="onChange" :value="page.hideLabels || false" style="margin: auto 0 auto 16px" />
		</div>
		
		<alert v-show="!isPro && tabs.length > 5">{{ $t('Only 5 tabs are allowed without Pro') }}.</alert>
		
		<q-tabs
			v-model="selectedTab"
			inline-label
			outside-arrows
			mobile-arrows
			class="bg-primary text-white full-width"
			align="left"
			>
			
			<draggable
				v-model="tabs"
				handle=".dragndrop"
				>
				
				<transition-group type="transition" name="flip-list" tag="div" style="display: flex; flex-wrap: wrap">
					<q-tab
						v-for="tab in tabs"
						:key="tab.id"
						:name="tab.id"
						:style="{ backgroundColor: tab.backgroundColor }"
						:class="{ 'rowDeleted': tab._deleted === true }"
						:disable="tab._deleted === true"
						>
						
						<template slot:default>
							<icon size="sm" name="mdi-drag-vertical" class="dragndrop" :color="tab.color" />
							<icon size="xs" :name="tab.icon" class="tabIcon" :color="tab.iconColor || tab.color" />
							<div class="q-tab__label" :style="{ color: tab.color }" v-show="page.hideLabels !== true">
								{{ tab.title }}
							</div>
							<div class="row items-center no-wrap" style="margin-left: 16px" :style="{ color: tab.color }">
								<inputs type="button" icon="mdi-content-copy" :tooltip="$t('copy')" @click="copyTab($event, tab.id)" v-show="tab._deleted !== true" />
								<inputs type="button" icon="mdi-folder-move-outline" :tooltip="$t('move')" @onSelect="moveTab(tab.id, ...arguments)" @click="stopPropagation" v-show="tab._deleted !== true && pagesDropdown.length > 0" :items="pagesDropdown" />
								<inputs type="button" icon="mdi-delete" :tooltip="$t('delete')" color="negative" @click="deleteTab($event, tab.id)" v-show="tab._deleted !== true" />
								<inputs type="button" icon="mdi-restore" :tooltip="$t('restore')" color="positive" @click="restoreTab($event, tab.id)" v-show="tab._deleted === true" />
							</div>
						</template>
					</q-tab>
				</transition-group>
			</draggable>
		</q-tabs>
		
		<q-tab-panels v-model="selectedTab" animated class="border">
			<q-tab-panel v-for="tab in tabs" :key="tab.id" :name="tab.id" style="padding: 16px 0" class="body">
				<tab-section :pageId="page.id" :tabId="tab.id" />
			</q-tab-panel>
		</q-tab-panels>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import Pro from 'src/controllers/Pro';

import TabSection from './TabSection.vue';

import Draggable from 'vuedraggable';

export default defineComponent({
	'name': 'DrawerPage',
	'props': [ 'pageId' ],
	
	'components': {
		Draggable,
		TabSection
	},
	
	data() {
		const page = this.$store.state.editor.layout.find(item => item.id === this.pageId) || {};
		return {
			'selectedTab': page.tabs?.[0]?.id || null
		}
	},
	
	'methods': {
		stopPropagation(e) {
			e.stopPropagation();
		},
		addTab() {
			this.$store.commit('editor/addTab', { 'pageId': this.pageId });
			
			// select added tab
			this.selectedTab = this.tabs[this.tabs.length - 1].id;
		},
		copyTab(e, tabId) {
			this.stopPropagation(e);
			this.$store.commit('editor/addTab', {
				'pageId': this.pageId,
				tabId
			});
			
			// select copied tab
			this.selectedTab = this.tabs[this.tabs.length - 1].id;
		},
		moveTab(tabId, props) {
			const { val } = props;
			this.$store.commit('editor/moveTab', {
				'pageId': this.pageId,
				tabId,
				'destinationPageId': val
			});
			
			// select moved tab
			// ?
		},
		deleteTab(e, tabId) {
			this.stopPropagation(e);
			this.$store.commit('editor/deleteTab', {
				'pageId': this.pageId,
				tabId
			});
			
			// unselect deleted tab
			if (this.selectedTab === tabId) {
				this.selectedTab = this.tabs.find(tab => tab._deleted !== true).id;
			}
		},
		restoreTab(e, tabId) {
			this.stopPropagation(e);
			this.$store.commit('editor/restoreTab', {
				'pageId': this.pageId,
				tabId
			});
			
			// select restored tab
			this.selectedTab = tabId;
		},
		onChange(props) {
			this.$store.commit('editor/editDrawerItem', {
				'itemId': this.pageId,
				'key': props.id,
				'val': props.value
			});
		}
	},
	
	'computed': {
		isPro() {
			return Pro.isPro();
		},
		page() {
			return this.$store.state.editor.layout.find(item => item.id === this.pageId) || {};
		},
		pagesDropdown() {
			const dropdown = [];
			this.$store.getters['editor/getPagesWithoutDeletions'].forEach(page => {
				if (page.id !== this.pageId) {
					dropdown.push({
						'id': page.id,
						'label': page.title,
						...page
					});
				}
			});
			
			return dropdown;
		},
		'tabs': {
			get() {
				const tabs = this.page.tabs || [];
				return tabs;
			},
			set(tabs) {
				this.$store.commit('editor/setTabs', {
					'pageId': this.pageId,
					tabs
				});
			}
		}
	},
	
	'watch': {
		pageId() {
			this.selectedTab = this.tabs?.[0]?.id || null;
		}
	}
});
</script>

<style lang="scss">
.border {
	border: 2px solid var(--q-color-primary);
}
</style>
