<template>
	<div style="padding: 0 8px">
		<div class="text-h6 primary q-ma-sm">{{ $t('Tab') }}: {{ tab.title }}</div>
		<div class="row">
			<div class="col">
				<inputs @onChange="onChange" id="icon" :label="$t('Tab Icon')" :value="tab.icon || ''" :icon="tab.icon" />
			</div>
			<div class="col">
				<inputs @onChange="onChange" id="title" :label="$t('Tab Title')" :value="tab.title || ''"  />
			</div>
		</div>
		<div class="row">
			<div class="col">
				<inputs type="color" @onChange="onChange" id="iconColor" :label="$t('Icon Color')" :value="tab.iconColor || ''"  />
			</div>
			<div class="col">
				<inputs type="color" @onChange="onChange" id="color" :label="$t('Font Color')" :value="tab.color || ''"  />
			</div>
			<div class="col">
				<inputs type="color" @onChange="onChange" id="backgroundColor" :label="$t('Background Color')" :value="tab.backgroundColor|| ''" />
			</div>
		</div>
		<div class="row">
			<div class="col-2">
				<inputs type="switch" inline :label="$t('Tab Fullscreen')" id="fullscreen" @onChange="onChange" :value="tab.fullscreen || false" style="margin: auto 0 auto 16px" />
			</div>
			<div class="col">
				<inputs type="switch" inline :label="$t('Widgets edges (round or angled)')" id="widgetEdges" @onChange="onChange" :value="tab.widgetEdges || false" style="margin: auto 0 auto 16px" />
			</div>
		</div>
		
		<div class="text-h6 primary q-ma-sm" style="margin-top: 24px">{{ $t('Widgets of tab') + ' ' + tab.title }}</div>
		<widget-section
			:pageId="pageId"
			:tabId="tabId"
			id="desktop"
			label="Desktop"
			icon="mdi-monitor"
			:fullscreen="tab.fullscreen || false"
			:widgetEdges="tab.widgetEdges || false"
			:widgetConfig="tab.widgetConfig || {}"
			:items="tab.widgetsDesktop"
			/>
		
		<widget-section
			:pageId="pageId"
			:tabId="tabId"
			id="smartphone"
			label="Smartphone"
			icon="mdi-cellphone"
			:fullscreen="tab.fullscreen || false"
			:widgetEdges="tab.widgetEdges || false"
			:widgetConfig="tab.widgetConfig || {}"
			:items="tab.widgetsSmartphone"
			/>
		
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import WidgetSection from './WidgetSection.vue';

export default defineComponent({
	'name': 'TabPage',
	'props': [ 'pageId', 'tabId' ],
	
	'components': {
		WidgetSection
	},
	
	'methods': {
		onChange(props) {
			this.$store.commit('editor/editTab', {
				'pageId': this.pageId,
				'tabId': this.tabId,
				'key': props.id,
				'val': props.value
			});
		}
	},
	
	'computed': {
		tab() {
			const drawer = this.$store.state.editor.layout.find(drawer => drawer.id === this.pageId) || {};
			const tab = drawer.tabs.find(tab => tab.id === this.tabId) || {};
			return tab;
		}
	}
});
</script>
