<template>
	<div class="full-height LayoutsPage">
		<div class="row no-wrap">
			<q-toolbar class="paper" style="min-height: 56px">
				<q-toolbar-title shrink>{{ $t('Layout') }}</q-toolbar-title>
			</q-toolbar>
		</div>
		
		<!-- PAGES LIST -->
		<div v-show="!expertMode" class="row" style="margin-bottom: 54px">
			<div class="col-3">
				<paper class="q-pa-sm">
					<div class="text-h6 primary q-ma-sm">
						{{ $t('Drawer') + ' / ' + $t('Pages') }}
					</div>
					
					<q-btn-group>
						<q-btn color="primary" icon="mdi-plus-circle" :label="$t('add page')" @click="addPage" />
						<q-btn color="secondary" icon="mdi-plus-circle" :label="$t('add header')" @click="addHeader" />
						<q-btn color="secondary" icon="mdi-plus-circle" :label="$t('add divider')" @click="addDivider" />
					</q-btn-group>
					
					<alert style="margin: 16px 0 0 0" v-show="!isPro && pages.length > 5">{{ $t('Only 5 items (page, header and divider) are allowed without Pro') }}.</alert>
					<q-separator style="margin: 16px 0" />
					
					<q-list>
						<draggable
							v-model="pages"
							handle=".dragndrop"
							>
							
							<transition-group type="transition" name="flip-list" tag="div">
								<q-item
									v-for="page of pages"
									:key="page.id"
									:clickable="!page.type || page.type === 'page'"
									@click="onSelectPage(page)"
									:class="{ 'rowDeleted': page._deleted === true, 'selected': selectedPage.id === page.id }"
									:disable="page._deleted === true"
									>
									
									<q-item-section avatar center>
										<icon size="sm" name="mdi-drag-horizontal" class="dragndrop" />
									</q-item-section>
									<q-item-section
										avatar
										center
										v-if="!page.type || page.type === 'page'"
										:style="{ 'backgroundColor': page.backgroundColor }"
										>
										
										<icon :name="page.icon" :color="selectedPage.id === page.id ? 'white' : 'primary'" :style="{ 'color': page.iconColor }" />
									</q-item-section>
									
									<q-item-section
										:class="{ 'itemDeleted': page._deleted === true }"
										:style="{ 'backgroundColor': page.backgroundColor, 'color': page.color }"
										>
										
										<q-separator v-if="page.type === 'divider'" />
										<q-item-label :class="{ header: page.type === 'header' }">{{ page.title }}</q-item-label>
									</q-item-section>
									
									<q-item-section side :class="{ 'rowDeleted': page._deleted === true, 'selected': selectedPage.id === page.id }">
										<div class="row items-center">
											<inputs type="button" icon="mdi-content-copy" :tooltip="$t('copy')" @click="copyItem($event, page.id)" v-show="page.type !== 'divider' && page._deleted !== true" />
											<inputs type="button" icon="mdi-delete" :tooltip="$t('delete')" color="negative" @click="deleteItem($event, page.id)" v-show="((page.type && page.type !== 'page') || ((!page.type || page.type === 'page') && pagesWithoutDeletionsCount > 1)) && page._deleted !== true" />
											<inputs type="button" icon="mdi-restore" :tooltip="$t('restore')" color="positive" @click="restoreItem($event, page.id)" v-show="page._deleted === true" class="restore" />
										</div>
									</q-item-section>
								</q-item>
							</transition-group>
						</draggable>
					</q-list>
				</paper>
			</div>
			<div class="col-9">
				<paper class="q-pa-sm">
					<drawer-section :pageId="selectedPage.id" />
				</paper>
			</div>
		</div>
		
		<!-- EXPERT MODE -->
		<paper v-show="expertMode" :style="{ 'height': (pageHeight-66-16) + 'px' }">
			<json-editor id="layout" :json="json" @onChange="onJsonEditor" />
		</paper>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import Pro from 'src/controllers/Pro';

import JsonEditor from 'src/components/json-editor.vue';
import DrawerSection from './DrawerSection.vue';
import Draggable from 'vuedraggable';

import i18n from './i18n';
export default defineComponent({
	'name': 'LayoutsPage',
	
	'components': {
		Draggable,
		DrawerSection,
		JsonEditor
	},
	
	data() {
		return {
			'selectedPage': this.$store.getters['editor/getLayout'][0] || {},
			'editItemId': null
		}
	},
	
	'methods': {
		stopPropagation(e) {
			e.stopPropagation();
		},
		onSelectPage(page) {
			this.selectedPage = page;
		},
		onJsonEditor(props) {
			if (!props._error) {
				this.$store.commit('editor/set', { [props.id]: JSON.parse(props.value) });
			}
		},
		addDivider() {
			this.$store.commit('editor/addDrawerItem', {
				'type': 'divider'
			});
		},
		addHeader() {
			this.$store.commit('editor/addDrawerItem', {
				'type': 'header',
				'title': this.$i18n.t('New Header')
			});
		},
		addPage() {
			this.$store.commit('editor/addDrawerItem', {
				'type': 'page',
				'icon': 'mdi-file',
				'title': this.$i18n.t('New Page'),
				'tabs': []
			});
			
			// select added page and add initial tab
			this.selectedPage = this.pages[this.pages.length - 1];
			this.$store.commit('editor/addTab', { 'pageId': this.selectedPage.id });
		},
		copyItem(e, itemId) {
			this.stopPropagation(e);
			this.$store.commit('editor/copyDrawerItem', { itemId });
			
			// select copied page
			const page = this.pages[this.pages.length - 1];
			if (page.type === 'page') {
				this.selectedPage = page;
			}
		},
		deleteItem(e, itemId) {
			this.stopPropagation(e);
			this.$store.commit('editor/deleteDrawerItem', { itemId });
			
			// unselect deleted page
			if (this.selectedPage.id === itemId) {
				this.selectedPage = this.pages.find(page => page.type === 'page' && page._deleted !== true);
			}
		},
		restoreItem(e, itemId) {
			this.stopPropagation(e);
			this.$store.commit('editor/restoreDrawerItem', { itemId });
			
			// select restored page
			const page = this.pages.find(page => page.id === itemId);
			if (page.type === 'page') {
				this.selectedPage = page;
			}
		},
		resetLayout() {
			this.$store.commit('editor/setLayout', this.$store.state.layout.drawers);
		}
	},
	
	'computed': {
		isPro() {
			return Pro.isPro();
		},
		pageHeight() {
			return this.$store.state.jarvis.pageHeight;
		},
		pagesWithoutDeletionsCount() {
			return this.$store.getters['editor/getPagesWithoutDeletions'].length;
		},
		'pages': {
			get() {
				const layout = this.$store.getters['editor/getLayout'] || [];
				return layout;
			},
			set(pages) {
				this.$store.commit('editor/setLayout', pages);
			}
		},
		json() {
			return this.$store.state.editor.layout;
		},
		expertMode() {
			return this.$store.state.editor.settings?.configExpertMode;
		}
	},
	
	created() {
		i18n.register(this.$i18n, i18n.translations);
	}
});
</script>

<style lang="scss">
.active {
	font-weight: bold;
}

.text {
	color: $primary;
}

.header {
	color: $grey-7;
	font-style: italic;
}

.selected {
	color: #fff;
}

.q-item.selected {
	background-color: var(--q-color-primary);
}
</style>
