<template>
	<div>
		<!-- WIDGET CONFIGURATION -->
		<widget-page
			@onClose="onWidgetClose"
			:widgetId="selectedWidgetId"
			/>
		
		<!-- WIDGET ASSIGNMENTS -->
		<q-dialog
			:value="items !== null"
			persistent
			>
			
			<q-card class="column" style="width: 1200px; max-width: 90vw;" v-if="items !== null">
				<q-card-section class="row items-center">
					<div class="text-h6">{{ $t('Assign Widget to Box') }}</div>
					
					<q-space />
					<q-btn flat round color="grey" icon="close" @click="cancel" />
				</q-card-section>
				<q-separator />
				
				<q-card-section class="col defaultPadding-2 q-pt-none scroll body">
					<div class="text-h7 primary q-ma-sm">{{ $t('Widget') }}</div>
					<q-separator style="margin: 0 0 8px 0" />
					
					<div class="row" v-show="itemList.length === 0">
						<div class="col">
							<inputs @onSelect="onChange" id="assignWidget" label="Assign Widget" type="select" :options="widgetList" :reset="reset" options-dense use-input />
						</div>
						<div class="col-1" style="text-align: center; line-height: 56px">{{ $t('or') }}</div>
						<div class="col-3" style="text-align: center; line-height: 56px">
							<q-btn color="primary" icon="mdi-plus-circle" :label="$t('add widget')" @click="onAddWidget" />
						</div>
					</div>
					
					<q-list>
						<q-item
							v-for="(item, i) in itemList"
							:key="item.id + '-' + i"
							class="list-group-item"
							>
							
							<q-item-section>
								<q-item-label>{{ item.title || $t('No Widget title') }}</q-item-label>
								<q-item-label caption>{{ item.module }}</q-item-label>
							</q-item-section>
							
							<q-item-section side>
								<div class="row items-center">
									<q-btn size="sm" flat round @click="onWidgetEdit(item.id)" icon="mdi-pencil" />
									<q-btn size="sm" flat round @click="onWidgetRemove(item.id)" icon="mdi-delete" color="negative" />
								</div>
							</q-item-section>
						</q-item>
					</q-list>
					
					<div class="text-h7 primary q-ma-sm" style="margin-top: 16px">{{ $t('Options') }}</div>
					<q-separator style="margin: 0 0 8px 0" />
					
					<div class="row q-pb-xs">
						<div class="col">
							<inputs v-show="itemList[0] && SCALE_EXCEPTIONS.includes(itemList[0].module) === false" icon="mdi-fit-to-page" @onChange="onChange" id="scaleToFitContents" label="Scale contents to fit widget" type="switch" :value="inputScaleToFitContents" />
							<em class="q-mx-md" v-show="!(itemList[0] && SCALE_EXCEPTIONS.includes(itemList[0].module) === false && ALIGNMENT_EXCEPTIONS.includes(itemList[0].module) === false)">{{ $t('No options') }}</em>
						</div>
						<div class="col">
							<inputs v-show="itemList[0] && ALIGNMENT_EXCEPTIONS.includes(itemList[0].module) === false" icon="mdi-arrow-split-horizontal" @onSelect="onChange" id="alignmentVertical" label="Vertical alignment" type="select" :options="alignmentVertical" :value="inputValueAlignmentVertical" />
						</div>
						<div class="col">
							<inputs v-show="itemList[0] && ALIGNMENT_EXCEPTIONS.includes(itemList[0].module) === false" icon="mdi-arrow-split-vertical" @onSelect="onChange" id="alignmentHorizontal" label="Horizontal alignment" type="select" :options="alignmentHorizontal" :value="inputValueAlignmentHorizontal" />
						</div>
					</div>
				</q-card-section>
				
				<q-separator />
				<q-card-actions align="right">
					<q-btn flat :label="$t('Cancel')" color="negative" @click="cancel" />
					<q-btn flat :label="$t('Apply')" color="primary" @click="save" :disable="hasErrors" />
				</q-card-actions>
			</q-card>
		</q-dialog>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import { v4 as uuid } from 'uuid';

import WidgetPage from 'src/pages/Configuration/WidgetsPage/WidgetPage/WidgetPage.vue';
import { ScaleExceptions as SCALE_EXCEPTIONS, AlignmentExceptions as ALIGNMENT_EXCEPTIONS } from 'src/modules';

const _rfdc = require('rfdc')();
export default defineComponent({
	'name': 'WidgetPopup',
	'props': [ 'widget' ],
	
	'components': {
		WidgetPage
	},
	
	data() {
		this.SCALE_EXCEPTIONS = SCALE_EXCEPTIONS;
		this.ALIGNMENT_EXCEPTIONS = ALIGNMENT_EXCEPTIONS;
		
		this.alignmentVertical = [
			{
				'label': this.$i18n.t('top aligned'),
				'value': 'top'
			}, {
				'label': this.$i18n.t('middle aligned'),
				'value': 'middle'
			}, {
				'label': this.$i18n.t('bottom aligned'),
				'value': 'bottom'
			}
		];
		
		this.alignmentHorizontal = [
			{
				'label': this.$i18n.t('left aligned'),
				'value': 'left'
			}, {
				'label': this.$i18n.t('center aligned'),
				'value': 'center'
			}, {
				'label': this.$i18n.t('right aligned'),
				'value': 'right'
			}, {
				'label': this.$i18n.t('fill full width'),
				'value': 'fill-width'
			}
		];
		
		return {
			'reset': null,
			'selectedWidgetId': null,
			'selectedWidgetShallBeAdded': false,
			'items': this.widget === null ? null : _rfdc(this.widget.items || [])
		}
	},
	
	'computed': {
		inputScaleToFitContents() {
			return this.widget.scaleToFitContents !== undefined ? this.widget.scaleToFitContents : true;
		},
		inputValueAlignmentVertical() {
			return this.widget.alignmentVertical || 'top';
		},
		inputValueAlignmentHorizontal() {
			return this.widget.alignmentHorizontal || 'center';
		},
		hasErrors() {
			return false;
		},
		widgetList() {
			const widgetList = Object.values(this.$store.state.editor.widgets)
				.filter(widget => this.items.indexOf(widget.id) === -1).map(widget => ({ 'value': widget.id, 'label': widget.module + (widget.title ? ' - ' + widget.title : '') + ' (' + widget.id.substr(0, 5) + ')' }))
				.sort((a, b) => {
					const left = a.label.toLowerCase();
					const right = b.label.toLowerCase();
					return left === right ? 0 : (left > right ? 1 : -1);
				});
			
			return widgetList;
		},
		itemList() {
			const itemList = this.items.map(itemId => {
				return this.$store.state.editor.widgets[itemId];
			});
			
			return itemList;
		}
	},
	
	'methods': {
		onChange(props) {
			// assign widget
			if (props.id === 'assignWidget' && props.value && this.items.indexOf(props.value) === -1) {
				this.items.push(props.value);
				
				// reset select box
				this.reset = Math.random();
			}
			
			// changing config
			else {
				this.$set(this.widget, props.id, props.value);
			}
		},
		onAddWidget() {
			this.selectedWidgetShallBeAdded = true;
			this.selectedWidgetId = uuid();
		},
		onWidgetClose(widget) {
			if (widget && this.selectedWidgetShallBeAdded === true) {
				this.onChange({ 'id': 'assignWidget', 'value': this.selectedWidgetId });
			}
			
			this.selectedWidgetShallBeAdded = false;
			this.selectedWidgetId = null;
		},
		cancel() {
			this.$emit('onClose');
			this.items = null;
		},
		save() {
			this.$emit('onSave', this.items);
			this.cancel();
		},
		onWidgetEdit(widgetId) {
			this.selectedWidgetId = widgetId;
		},
		onWidgetRemove(widgetId) {
			const confirmation = confirm(this.$i18n.t('Really delete item?'));
			if (confirmation) {
				this.items.splice(this.items.indexOf(widgetId), 1);
			}
		}
	},
	
	'watch': {
		widget(widget) {
			this.items = widget === null ? null : _rfdc(widget.items || []);
		}
	}
});
</script>
